"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { PayrollCalculator, type PayrollCalculation } from "@/lib/payroll-calculator";
import { format } from "date-fns";
import { es } from "date-fns/locale";

interface PayrollRecord {
  id: string;
  employeeId: string;
  employeeName: string;
  period: string;
  grossSalary: number;
  netSalary: number;
  status: "pending" | "processed" | "paid";
}

export default function PayrollPage() {
  const [selectedPeriod, setSelectedPeriod] = useState<string>("biweekly");
  const [processing, setProcessing] = useState(false);

  // Mock data - replace with actual API calls
  const mockPayrollRecords: PayrollRecord[] = [
    {
      id: "PR001",
      employeeId: "EMP001",
      employeeName: "Juan Pérez",
      period: "1-15 Feb 2024",
      grossSalary: 15000,
      netSalary: 12500,
      status: "paid",
    },
    {
      id: "PR002",
      employeeId: "EMP002",
      employeeName: "María García",
      period: "1-15 Feb 2024",
      grossSalary: 18000,
      netSalary: 15000,
      status: "processed",
    },
  ];

  const handleProcessPayroll = async () => {
    setProcessing(true);
    try {
      // Example calculation for one employee
      const calculator = new PayrollCalculator(
        15000, // Monthly base salary
        {
          type: "biweekly",
          startDate: new Date(),
          endDate: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000),
        },
        {
          regular: 80, // Regular hours in two weeks
          overtime: 5,
          holidayTime: 0,
        },
        2, // Years of service
        {
          absences: 0,
          lateArrivals: 1,
        }
      );

      const result = calculator.calculatePayroll();
      console.log("Payroll calculated:", result);
      
      // Here you would typically send this to your backend
      await new Promise(resolve => setTimeout(resolve, 1500)); // Simulate API call
      
    } catch (error) {
      console.error("Error processing payroll:", error);
    } finally {
      setProcessing(false);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("es-MX", {
      style: "currency",
      currency: "MXN",
    }).format(amount);
  };

  const getStatusBadgeClass = (status: PayrollRecord["status"]) => {
    const classes = {
      pending: "bg-yellow-100 text-yellow-800",
      processed: "bg-blue-100 text-blue-800",
      paid: "bg-green-100 text-green-800",
    };
    return classes[status];
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h1 className="text-3xl font-bold">Procesamiento de Nómina</h1>
          <p className="text-gray-600">Gestione y procese los pagos de nómina</p>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <Card>
          <CardHeader>
            <CardTitle>Nóminas Pendientes</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold">2</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Nóminas Procesadas</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold text-blue-600">15</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Total Empleados</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold">25</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Monto Total</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-3xl font-bold text-green-600">
              {formatCurrency(275000)}
            </p>
          </CardContent>
        </Card>
      </div>

      <Card className="mb-8">
        <CardHeader>
          <CardTitle>Procesar Nueva Nómina</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex space-x-4">
            <Select
              value={selectedPeriod}
              onValueChange={setSelectedPeriod}
            >
              <SelectTrigger className="w-[200px]">
                <SelectValue placeholder="Seleccione período" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="weekly">Semanal</SelectItem>
                <SelectItem value="biweekly">Quincenal</SelectItem>
                <SelectItem value="monthly">Mensual</SelectItem>
              </SelectContent>
            </Select>
            <Button
              onClick={handleProcessPayroll}
              disabled={processing}
            >
              {processing ? "Procesando..." : "Procesar Nómina"}
            </Button>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Historial de Nómina</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>ID Nómina</TableHead>
                <TableHead>Empleado</TableHead>
                <TableHead>Período</TableHead>
                <TableHead>Salario Bruto</TableHead>
                <TableHead>Salario Neto</TableHead>
                <TableHead>Estado</TableHead>
                <TableHead>Acciones</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {mockPayrollRecords.map((record) => (
                <TableRow key={record.id}>
                  <TableCell>{record.id}</TableCell>
                  <TableCell>{record.employeeName}</TableCell>
                  <TableCell>{record.period}</TableCell>
                  <TableCell>{formatCurrency(record.grossSalary)}</TableCell>
                  <TableCell>{formatCurrency(record.netSalary)}</TableCell>
                  <TableCell>
                    <span
                      className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusBadgeClass(
                        record.status
                      )}`}
                    >
                      {record.status.charAt(0).toUpperCase() + record.status.slice(1)}
                    </span>
                  </TableCell>
                  <TableCell>
                    <Button variant="outline" size="sm">
                      Ver Detalles
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
