import {
  calculateVacationDays,
  calculateVacationPremium,
  calculateOvertime,
  calculateChristmasBonus,
  calculateSocialSecurity,
  calculateAttendanceBonus,
  calculatePunctualityBonus,
  type SocialSecurityContributions
} from './labor-laws';

interface PayrollPeriod {
  type: 'weekly' | 'biweekly' | 'monthly';
  startDate: Date;
  endDate: Date;
}

interface WorkedHours {
  regular: number;
  overtime: number;
  holidayTime: number;
}

interface Deductions {
  socialSecurity: number;
  incomeTax: number;
  loans?: number;
  other?: number;
}

interface Bonuses {
  attendance?: number;
  punctuality?: number;
  performance?: number;
  other?: number;
}

export interface PayrollCalculation {
  grossSalary: number;
  netSalary: number;
  deductions: Deductions;
  bonuses: Bonuses;
  socialSecurity: SocialSecurityContributions;
  overtime: number;
  holidayPay: number;
  vacationPremium?: number;
  christmasBonus?: number;
}

export class PayrollCalculator {
  // ISR (Income Tax) brackets for 2024 (example rates - should be updated with current rates)
  private static readonly ISR_BRACKETS = [
    { limit: 7735.00, rate: 0.0192 },
    { limit: 65651.07, rate: 0.0640 },
    { limit: 115375.90, rate: 0.1088 },
    { limit: 134119.41, rate: 0.16 },
    { limit: 160577.65, rate: 0.1792 },
    { limit: 323862.00, rate: 0.2136 },
    { limit: 510451.00, rate: 0.2352 },
    { limit: Infinity, rate: 0.35 }
  ];

  // UMA Value for 2024 (Update annually)
  private static readonly UMA = 103.74;

  constructor(
    private baseSalary: number,
    private period: PayrollPeriod,
    private workedHours: WorkedHours,
    private yearsOfService: number,
    private attendanceRecord: {
      absences: number;
      lateArrivals: number;
    }
  ) {}

  public calculatePayroll(): PayrollCalculation {
    const regularPay = this.calculateRegularPay();
    const overtimePay = this.calculateOvertimePay();
    const holidayPay = this.calculateHolidayPay();
    const bonuses = this.calculateBonuses();
    const deductions = this.calculateDeductions(regularPay + overtimePay + holidayPay);
    const socialSecurity = this.calculateSocialSecurityContributions();

    const grossSalary = regularPay + overtimePay + holidayPay + 
      Object.values(bonuses).reduce((sum, value) => sum + (value || 0), 0);

    const totalDeductions = deductions.socialSecurity + deductions.incomeTax +
      (deductions.loans || 0) + (deductions.other || 0);

    return {
      grossSalary,
      netSalary: grossSalary - totalDeductions,
      deductions,
      bonuses,
      socialSecurity,
      overtime: overtimePay,
      holidayPay,
      vacationPremium: this.calculateVacationPremiumIfApplicable(),
      christmasBonus: this.calculateChristmasBonusIfApplicable()
    };
  }

  private calculateRegularPay(): number {
    const daysInPeriod = this.getDaysInPeriod();
    const dailySalary = this.baseSalary / 30; // Convert monthly salary to daily
    return dailySalary * daysInPeriod;
  }

  private calculateOvertimePay(): number {
    const hourlyRate = (this.baseSalary / 30) / 8; // Daily salary divided by 8 hours
    return calculateOvertime(
      hourlyRate,
      this.workedHours.overtime,
      this.workedHours.overtime > 9
    );
  }

  private calculateHolidayPay(): number {
    const hourlyRate = (this.baseSalary / 30) / 8;
    return this.workedHours.holidayTime * (hourlyRate * 3); // Triple pay for holiday work
  }

  private calculateBonuses(): Bonuses {
    return {
      attendance: calculateAttendanceBonus(
        this.baseSalary,
        this.attendanceRecord.absences === 0
      ),
      punctuality: calculatePunctualityBonus(
        this.baseSalary,
        this.attendanceRecord.lateArrivals
      )
    };
  }

  private calculateDeductions(totalEarnings: number): Deductions {
    return {
      socialSecurity: this.calculateSocialSecurityContributions().employeeContribution,
      incomeTax: this.calculateIncomeTax(totalEarnings)
    };
  }

  private calculateSocialSecurityContributions(): SocialSecurityContributions {
    return calculateSocialSecurity(this.baseSalary);
  }

  private calculateIncomeTax(income: number): number {
    // Find applicable tax bracket
    const bracket = PayrollCalculator.ISR_BRACKETS.find(b => income <= b.limit);
    if (!bracket) return 0;
    return income * bracket.rate;
  }

  private calculateVacationPremiumIfApplicable(): number | undefined {
    const vacationDays = calculateVacationDays(this.yearsOfService);
    return calculateVacationPremium(this.baseSalary, vacationDays);
  }

  private calculateChristmasBonusIfApplicable(): number | undefined {
    // Only calculate if it's December or specified by company policy
    const isDecember = new Date().getMonth() === 11;
    if (!isDecember) return undefined;
    
    return calculateChristmasBonus(this.baseSalary, 365); // Assuming full year
  }

  private getDaysInPeriod(): number {
    switch (this.period.type) {
      case 'weekly':
        return 7;
      case 'biweekly':
        return 14;
      case 'monthly':
        return 30;
      default:
        throw new Error('Invalid period type');
    }
  }

  // Additional helper methods for specific Mexican labor law requirements

  public calculateSeverancePay(isJustifiedDismissal: boolean): number {
    const dailySalary = this.baseSalary / 30;
    return isJustifiedDismissal
      ? dailySalary * 90 // 3 months salary
      : (dailySalary * 90) + (dailySalary * 20 * this.yearsOfService); // 3 months + 20 days per year
  }

  public calculateProRatedBenefits(daysWorked: number): {
    christmasBonus: number;
    vacationPremium: number;
  } {
    const christmasBonus = calculateChristmasBonus(this.baseSalary, daysWorked);
    const vacationDays = calculateVacationDays(this.yearsOfService);
    const vacationPremium = calculateVacationPremium(this.baseSalary, vacationDays);
    
    return {
      christmasBonus: (christmasBonus * daysWorked) / 365,
      vacationPremium: (vacationPremium * daysWorked) / 365
    };
  }

  public static calculateSettlementPay(
    monthlySalary: number,
    yearsOfService: number,
    daysWorkedInCurrentYear: number,
    pendingVacationDays: number,
    isJustifiedDismissal: boolean
  ): {
    severance: number;
    proRatedChristmasBonus: number;
    vacationPremium: number;
    pendingVacationDays: number;
    total: number;
  } {
    const dailySalary = monthlySalary / 30;
    
    // Calculate severance
    const severance = isJustifiedDismissal
      ? dailySalary * 90
      : (dailySalary * 90) + (dailySalary * 20 * yearsOfService);

    // Calculate pro-rated Christmas bonus
    const proRatedChristmasBonus = (dailySalary * 15 * daysWorkedInCurrentYear) / 365;

    // Calculate vacation premium
    const vacationPremium = calculateVacationPremium(
      monthlySalary,
      pendingVacationDays
    );

    // Calculate pending vacation days payment
    const pendingVacationDaysAmount = dailySalary * pendingVacationDays;

    const total = severance + proRatedChristmasBonus + vacationPremium + pendingVacationDaysAmount;

    return {
      severance,
      proRatedChristmasBonus,
      vacationPremium,
      pendingVacationDays: pendingVacationDaysAmount,
      total
    };
  }
}
