// Types for employee-related data
export interface Employee {
  id: string;
  name: string;
  startDate: Date;
  salary: number;
  workSchedule: WorkSchedule;
}

export interface WorkSchedule {
  type: 'diurno' | 'nocturno' | 'mixto';
  hoursPerDay: number;
  daysPerWeek: number;
}

// Vacation calculation according to LFT Art. 76
export function calculateVacationDays(yearsOfService: number): number {
  // Base vacation days according to Mexican Labor Law
  const vacationTable = new Map<number, number>([
    [1, 12], // 1 year = 12 days
    [2, 14], // 2 years = 14 days
    [3, 16], // 3 years = 16 days
    [4, 18], // 4 years = 18 days
    [5, 20], // 5 years = 20 days
    // After 5 years, add 2 days per 5 years of service
  ]);

  if (yearsOfService <= 5) {
    return vacationTable.get(yearsOfService) || 0;
  }

  // Calculate additional days for service over 5 years
  const baseVacationDays = 20; // Base days for 5 years
  const additionalYears = yearsOfService - 5;
  const additionalDays = Math.floor(additionalYears / 5) * 2;

  return baseVacationDays + additionalDays;
}

// Calculate vacation premium (Prima Vacacional) - 25% of salary for vacation period
export function calculateVacationPremium(salary: number, vacationDays: number): number {
  const dailySalary = salary / 30; // Monthly salary converted to daily
  return (dailySalary * vacationDays * 0.25);
}

// Overtime calculations according to LFT Art. 67
export function calculateOvertime(
  regularHourlyRate: number,
  overtimeHours: number,
  isTripleTime: boolean = false
): number {
  // First 9 overtime hours in a week are paid double
  // Hours beyond that are paid triple
  const doubleTimeLimit = 9;
  
  if (!isTripleTime && overtimeHours <= doubleTimeLimit) {
    return overtimeHours * (regularHourlyRate * 2);
  }

  if (isTripleTime || overtimeHours > doubleTimeLimit) {
    const doubleTimeHours = Math.min(overtimeHours, doubleTimeLimit);
    const tripleTimeHours = Math.max(0, overtimeHours - doubleTimeLimit);
    
    return (doubleTimeHours * (regularHourlyRate * 2)) + 
           (tripleTimeHours * (regularHourlyRate * 3));
  }

  return 0;
}

// Calculate severance pay (Indemnización) according to LFT Art. 50
export function calculateSeverancePay(
  dailySalary: number,
  yearsOfService: number,
  isJustifiedDismissal: boolean
): number {
  if (isJustifiedDismissal) {
    return dailySalary * 12; // 3 months salary
  }

  // Unjustified dismissal:
  // 3 months salary + 20 days per year of service
  const threeMothsSalary = dailySalary * 90;
  const serviceCompensation = dailySalary * 20 * yearsOfService;
  
  return threeMothsSalary + serviceCompensation;
}

// Calculate Christmas Bonus (Aguinaldo) according to LFT Art. 87
export function calculateChristmasBonus(
  monthlySalary: number,
  daysWorkedInYear: number
): number {
  const fullYearDays = 365;
  const minimumDays = 15; // Minimum days of aguinaldo required by law
  
  // Prorated calculation if worked less than a full year
  return (monthlySalary / 30) * minimumDays * (daysWorkedInYear / fullYearDays);
}

// Calculate social security contributions
export interface SocialSecurityContributions {
  employerContribution: number;
  employeeContribution: number;
  total: number;
}

export function calculateSocialSecurity(
  baseSalary: number,
  riskPremium: number = 0.01
): SocialSecurityContributions {
  // These are simplified rates - actual rates vary by risk class and other factors
  const employerRates = {
    disability: 0.0175,    // Invalidez y Vida
    medical: 0.0105,       // Gastos Médicos
    daycare: 0.01,         // Guarderías
    retirement: 0.02,      // Retiro
    risk: riskPremium      // Prima de Riesgo de Trabajo (varies by company)
  };

  const employeeRates = {
    disability: 0.00625,   // Invalidez y Vida
    medical: 0.00375,      // Gastos Médicos
    retirement: 0.01125    // Retiro
  };

  const employerContribution = baseSalary * (
    employerRates.disability +
    employerRates.medical +
    employerRates.daycare +
    employerRates.retirement +
    employerRates.risk
  );

  const employeeContribution = baseSalary * (
    employeeRates.disability +
    employeeRates.medical +
    employeeRates.retirement
  );

  return {
    employerContribution,
    employeeContribution,
    total: employerContribution + employeeContribution
  };
}

// Attendance status types
export type AttendanceStatus = 
  | 'presente'           // Present
  | 'ausente'           // Absent
  | 'retardo'           // Late
  | 'vacaciones'        // Vacation
  | 'incapacidad'       // Medical leave
  | 'permiso'           // Authorized leave
  | 'descanso'          // Rest day
  | 'festivo';          // Holiday

// Incident types according to Mexican labor law
export type IncidentType = 
  | 'vacaciones'        // Vacations
  | 'incapacidad'       // Medical leave
  | 'maternidad'        // Maternity leave
  | 'paternidad'        // Paternity leave
  | 'permiso_con_goce'  // Paid leave
  | 'permiso_sin_goce'  // Unpaid leave
  | 'suspension'        // Suspension
  | 'falta'            // Absence
  | 'retardo';         // Late arrival

// Interface for incident records
export interface IncidentRecord {
  employeeId: string;
  type: IncidentType;
  startDate: Date;
  endDate: Date;
  status: 'pendiente' | 'aprobado' | 'rechazado';
  documentation?: string;
  comments?: string;
}

// Calculate attendance bonus if applicable
export function calculateAttendanceBonus(
  baseSalary: number,
  perfectAttendance: boolean,
  maxBonusPercentage: number = 0.10
): number {
  return perfectAttendance ? baseSalary * maxBonusPercentage : 0;
}

// Calculate punctuality bonus if applicable
export function calculatePunctualityBonus(
  baseSalary: number,
  lateArrivals: number,
  maxBonusPercentage: number = 0.05
): number {
  // No bonus if there are any late arrivals
  return lateArrivals === 0 ? baseSalary * maxBonusPercentage : 0;
}
